import React, { useRef } from 'react'

import Container from '../../components/Container'
import Box from '../../components/Box'
import Image from '../../components/Image'
import Button from '../../components/Button'
import Text from '../../components/Text'
import Arrow from '../../components/Arrow'
import BackgroundImage from '../../components/BackgroundImage'
import { responsive, Media } from '../../components/ThemeProvider/theme'

// import web from './web.png'
// import mobile from './mobile.png'
import a from './a.svg'
import aMobile from './a-mobile.svg'
import b from './b.svg'
import joint from './joint.svg'
// import useHeaderHeight from '../useHeaderHeight'

const Intro = () => {
  const scrollRef = useRef()

  return (
    <Container pt="2em" textAlign="center">
      <Box>
        <Text.H2 fontWeight="bold" borderBottom="1px solid" borderColor="darkBlue" textAlign="left" pb="0.5em" mb="1em">
          <Text.Inline color="green">機會與風險</Text.Inline>
          <Text.Inline ml="0.125em" color="darkBlue">怕泡沫更怕錯過</Text.Inline>
        </Text.H2>
        <Text textAlign="left">面對機會與風險並存的市場，投資難度大幅攀升，各國股市不斷創高點，投資人怕泡沫更怕錯過。</Text>
        <Image src={b} />
        <Text textAlign="left" mt="1em">同時，超低利時代持續中，追求收益以及低波動的投資人更需要多元選擇才能掌握收益機會。</Text>
        <Media at="mobile">
          <Image src={aMobile} />
        </Media>
        <Media greaterThan="mobile">
          <Image src={a} />
        </Media>
        <Button
          my="1em"
          rightIcon={<Arrow width="1em" />}
          onClick={() => scrollRef.current.scrollIntoView({ behavior: 'smooth' })}
          className="intro-cta"
        >實現更靈活、低波動的投資策略</Button>
      </Box>
      <Box ref={scrollRef} py="2em">
        <Text.H3 color="prussianBlue">
          在風險中把握機會<br />
          施羅德打造以收益為主的多重資產基金
        </Text.H3>
        <Box mx={responsive('0', '10%', '20%')} mt="1em">
          <Image src={joint} />
        </Box>
      </Box>
    </Container>
  )
}

export default Intro
